import Paper from '@material-ui/core/Paper';
import Popper, { PopperPlacementType } from '@material-ui/core/Popper';
import { withStyles } from '@material-ui/core/styles';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import GoogleMapReact from 'google-map-react';
import React, { useState } from 'react';

import { Color } from '../styles/styles';

export interface Props {
  classes: any,
  center?: {
    lat: number;
    lng: number;
  };
  zoom?: number;
  title: string;
}

const styles: object = (theme) => ({
  stationContainer: {
    display: 'flex',
    flexDirection: 'column',
    padding: '1em 0 1em 0',
    width: 180,
    backgroundColor: Color.secondColor,
    boxShadow: '0px 2px 4px -1px rgba(0,0,0,0.2),'
      + '0px 4px 5px 0px rgba(0,0,0,0.14),'
      + '0px 1px 10px 0px rgba(0,0,0,0.12)',
  },
  stationTitle: {
    margin: 4,
    textAlign: 'center',
    color: Color.gray,
    fontSize: 14,
  },
  stationButton: {
    margin: 12,
    backgroundColor: Color.cyan,
    '&:hover': {
      backgroundColor: Color.cyan700,
    },
  },
  stationIcon: {
    color: Color.cyan700,
  }
});

const StationView = ({
  classes,
  title,
}: Props): React.ReactElement => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);
  const [open, setOpen] = React.useState(false);
  const [placement, setPlacement] = React.useState<PopperPlacementType>();
  const handleClick = (newPlacement: PopperPlacementType) => (
    event: React.MouseEvent<HTMLButtonElement>,
  ) => {
    console.log('abcc');
    setAnchorEl(event.currentTarget);
    setOpen(prev => placement !== newPlacement || !prev);
    setPlacement(newPlacement);
  };

  return (
    <>
      <Popper open={open} anchorEl={anchorEl} placement={placement} transition>
        {({ TransitionProps }) => (
          <Fade {...TransitionProps} timeout={350}>
            <Paper className={classes.stationContainer}>
              <Typography
                className={classes.stationTitle}>
                {title}
              </Typography>
              <Button
                variant="contained"
                className={classes.stationButton}
              >
                Data
              </Button>
              <Button
                variant="contained"
                className={classes.stationButton}
              >
                Enhet
              </Button>
            </Paper>
          </Fade>
        )}
      </Popper>
      <LocationOnIcon
        fontSize={'large'}
        className={classes.stationIcon}
        onClick={handleClick('right')}
      />
    </>
  );
};

export default withStyles(styles)(StationView);
